.dark{
    @apply bg-gray-900 text-white;
}
.light{
    @apply bg-white text-gray-900;
}
.nav.light{
    @apply bg-slate-50 text-gray-800;
}
.nav.dark{
    @apply bg-gray-800 text-slate-50;
}
.stars{
    @apply text-white drop-shadow-[0_1.2px_1.2px_rgba(0,0,0)] cursor-pointer hover:text-yellow-300;
}
.stars.active{
    @apply text-yellow-300;
}